@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap";
*, :root {
  --text-red-warn: #ff001b;
  --text-orange: #ff6b08;
  --text-black: #000;
  --text-white: #fff;
  --text-footer: #545454;
  --bg-white: #fff;
  --bg-gray: #e5e5e5;
  --bg-modal-inside: #f7f7f7;
}

:after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-size: 100%;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

.section {
  padding-top: 94px;
  padding-bottom: 94px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    min-width: 1200px;
  }
}

.footer {
  height: 100px;
  color: var(--text-footer);
  background-color: #f7f7f7;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: .875rem;
  line-height: .875;
  display: flex;
}

.footer__link {
  color: var(--text-footer);
  text-decoration: underline;
}

.footer__icon {
  color: var(--text-red-warn);
}

@media screen and (min-width: 768px) {
  .footer {
    height: 79px;
    flex-direction: row;
    font-size: 1rem;
    line-height: 1.187;
  }

  .footer > p {
    margin: 0 0 0 3px;
  }
}

@media screen and (min-width: 1200px) {
  .footer {
    height: 77px;
  }
}

dialog::backdrop {
  background-color: #00000080;
}

.centered {
  justify-content: center;
  align-items: center;
  display: flex;
}

.centered--column {
  flex-direction: column;
}

.footer-modal {
  max-width: 900px;
  background-color: var(--bg-modal-inside);
  color: var(--text-footer);
  border: 1px solid #333;
  border-radius: 5px;
  margin: 0 auto;
  padding: 20px;
  position: fixed;
  top: 20px;
}

.footer-modal > p {
  margin: 10px 0 0 3px;
}

.footer-modal__closeBtn {
  border-radius: 50%;
  position: absolute;
  top: 10px;
  right: 10px;
}

.footer-modal__closeBtn:hover {
  fill: var(--text-red-warn);
  cursor: pointer;
}

.our-team__tittle {
  margin-bottom: 15px;
}

.our-team__list {
  flex-wrap: wrap;
  list-style: none;
  display: flex;
}

.our-team__item {
  max-width: 160px;
  height: 160px;
  background-color: var(--text-white);
  border-radius: 4px;
  margin: 10px;
  padding: 15px;
  box-shadow: 8px 9px 20px -3px #42445a;
}

.our-team__img {
  border-radius: 50%;
}

.our-team__card {
  width: 160px;
}

.our-team__name {
  color: var(--text-orange);
  margin: 10px auto;
  font-size: .75rem;
}

.our-team__speciality {
  margin: 0;
  padding-bottom: 6px;
  font-size: .68rem;
  font-weight: 600;
}

@media screen and (max-width: 768px) {
  .footer-modal {
    width: 200px;
  }
}

@media screen and (max-width: 1200px) {
  .footer-modal {
    max-width: 500px;
  }
}

